<template>
  <v-container class="my-10 font-weight-light">
    <StatusData />

    <div v-for="d of citytests" :key="d.city">
      <v-simple-table class="mt-10">
        <template v-slot:default>
          <tbody>
            <tr>
              <td width="50%" class="font-weight-bold">
                {{ d.city_name.toUpperCase() }}
              </td>
              <td width="25%">{{ d.amount_of_tests }}</td>
              <td width="25%">
                <v-btn icon :to="`/data/${d.city_name.toLowerCase()}`"
                  ><v-icon>mdi-view-list</v-icon></v-btn
                >
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </div>
  </v-container>
</template>

<script>
import axios from 'axios';
import StatusData from '@/components/StatusData.vue';
export default {
  name: 'Home',
  components: {
    StatusData,
  },
  created() {
    this.getCitiesTests();
  },
  data() {
    return {
      serveraddress: process.env.VUE_APP_SERVER,
      citytests: [],
    };
  },
  methods: {
    async getCitiesTests() {
      const { data } = await axios({
        url: `${this.serveraddress}/tests/all/amount`,
        method: 'GET',
      });
      this.citytests = data;
    },
  },
};
</script>
